import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Input } from "./ui/input.js";
import { Button } from "./ui/button.js";
import { Card, CardContent } from "./ui/card.js";
import { AnimatePresence, motion } from "framer-motion";
import { Search, Mic, ThumbsUp, ThumbsDown } from "lucide-react";
import "../styles/faq.css";
import SweetPopup from "./sweetpopup.js"; // ✅ Import SweetPopup

const faqs = [
  {
    id: 1,
    question: "How long does it take to get my car?",
    answer:
      "🚗 Most deliveries are done within 5–10 working days. Want it faster? We have express options ready.",
  },
  {
    id: 2,
    question: "Will the car be ready to drive?",
    answer:
      "🔧 Yes. Every car is fully inspected, clean, and delivered with valid papers — no stress, no surprises.",
  },
  {
    id: 3,
    question: "Do you help after delivery?",
    answer:
      "🤝 Absolutely. From ownership updates to support, we’re with you — even after you get the car.",
  },
  {
    id: 4,
    question: "What makes Donvickey Autos different?",
    answer:
      "🏆 We don’t just sell cars. We protect your trust. Every deal is clean, sharp, and handled like family.",
  },
  {
    id: 5,
    question: "I’m not sure what car I want. Can you help?",
    answer:
      "🧭 Yes. Tell us your budget, needs, or taste — we’ll guide you to the perfect choice.",
  },
  {
    id: 6,
    question: "How much do your cars cost?",
    answer:
      "💼 Prices depend on model, condition, and specs — but we give you honest deals, with no hidden stress.",
  },
  {
    id: 7,
    question: "What’s the next step?",
    answer:
      "📞 Simple. Call or chat with us. Let’s help you drive the car you deserve.",
  },
];

export default function UltimateFAQ() {
  const [search, setSearch] = useState("");
  const [selectedFAQ, setSelectedFAQ] = useState(null);
  const [feedback, setFeedback] = useState({});
  const [isListening, setIsListening] = useState(false);
  const [isPopupOpen, setPopupOpen] = useState(false); // ✅ State for popup
  let recognition;

  useEffect(() => {
    if ("webkitSpeechRecognition" in window || "SpeechRecognition" in window) {
      recognition = new (window.SpeechRecognition ||
        window.webkitSpeechRecognition)();
      recognition.continuous = false;
      recognition.interimResults = false;
      recognition.lang = "en-US";

      recognition.onstart = () => setIsListening(true);
      recognition.onend = () => setIsListening(false);
      recognition.onresult = (event) =>
        setSearch(event.results[0][0].transcript);
      recognition.onerror = () => setIsListening(false);
    }
  }, []);

  const startListening = () => {
    if (recognition) recognition.start();
    else alert("Your browser does not support speech recognition.");
  };

  const handleFeedback = (id, type) => {
    setFeedback((prev) => ({ ...prev, [id]: prev[id] === type ? null : type }));
  };

  const filteredFAQs = search
    ? faqs.filter((faq) =>
        faq.question.toLowerCase().includes(search.toLowerCase())
      )
    : faqs.slice(0, 4);

  const navigate = useNavigate();

  return (
    <div className="faq-container">
      <h1 className="faq-title">Got Questions? Let’s Clear Them Up.</h1>
      <p className="faq-description">
        At Donvickey Autos, we help you buy smart, drive sharp, and stay
        confident — every step of the way.
      </p>

      <div className="faq-search-container">
        <Input
          className="faq-search-input"
          placeholder="Search FAQs..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
        />
        <Search className="faq-search-icon" />
        <Mic
          className={`faq-mic-icon ${isListening ? "listening" : ""}`}
          onClick={startListening}
        />
      </div>

      <AnimatePresence>
        {filteredFAQs.map((faq) => (
          <motion.div
            key={faq.id}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.3 }}
          >
            <Card className="faq-card">
              <CardContent
                className="faq-question"
                onClick={() =>
                  setSelectedFAQ(selectedFAQ === faq.id ? null : faq.id)
                }
              >
                <span>{faq.question}</span>
              </CardContent>
              {selectedFAQ === faq.id && (
                <motion.div
                  initial={{ height: 0, opacity: 0 }}
                  animate={{ height: "auto", opacity: 1 }}
                  exit={{ height: 0, opacity: 0 }}
                  transition={{ duration: 0.3 }}
                  className="faq-answer"
                >
                  <p>{faq.answer}</p>
                  <div className="faq-feedback">
                    <ThumbsUp
                      className={`feedback-icon ${
                        feedback[faq.id] === "like" ? "liked" : ""
                      }`}
                      onClick={() => handleFeedback(faq.id, "like")}
                    />
                    <ThumbsDown
                      className={`feedback-icon ${
                        feedback[faq.id] === "dislike" ? "disliked" : ""
                      }`}
                      onClick={() => handleFeedback(faq.id, "dislike")}
                    />
                  </div>
                </motion.div>
              )}
            </Card>
          </motion.div>
        ))}
      </AnimatePresence>

      <Button
        className="faq-cta-button"
        onClick={() => setPopupOpen(true)} // ✅ Trigger SweetPopup
      >
        Still Need Help? Talk to Us!
      </Button>

      {/* ✅ SweetPopup rendered here */}
      <SweetPopup isOpen={isPopupOpen} onClose={() => setPopupOpen(false)} />
    </div>
  );
}
