import React, { useState } from "react";
import { motion } from "framer-motion";
import {
  FaHandshake,
  FaRegLightbulb,
  FaVideo,
  FaCut,
  FaShareAlt,
} from "react-icons/fa";
import "../styles/workprocess.css";
import SweetPopup from "../components/sweetpopup.js"; // ✅ Import SweetPopup

const steps = [
  {
    icon: <FaHandshake />,
    title: "Let’s Talk",
    description:
      "We hear you out — what you want, your budget, and the kind of car that fits you best.",
  },
  {
    icon: <FaRegLightbulb />,
    title: "Plan Your Perfect Ride",
    description:
      "We help you choose smart — clean options, full checks, no hidden issues.",
  },
  {
    icon: <FaVideo />,
    title: "Inspect & Confirm",
    description:
      "Before any payment, we let you see everything — engine, papers, the full truth.",
  },
  {
    icon: <FaCut />,
    title: "Seal It Clean",
    description:
      "We finalize your deal with verified documents and zero stress.",
  },
  {
    icon: <FaShareAlt />,
    title: "Drive with Confidence",
    description:
      "You get your car, fully ready. No stories. No drama. Just pure satisfaction.",
  },
];

const WorkProcess = () => {
  const [isPopupOpen, setPopupOpen] = useState(false); // ✅ Popup state

  const handleCTAClick = (e) => {
    const isMobile = /Mobi|Android/i.test(navigator.userAgent);
    if (!isMobile) {
      e.preventDefault(); // ❌ Block call on desktop
      setPopupOpen(true); // ✅ Show SweetPopup instead
    }
  };

  return (
    <div className="work-process-section">
      <motion.h2
        initial={{ opacity: 0, y: -50 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6 }}
        className="section-title"
      >
        How We Deliver Results
      </motion.h2>

      <div className="process-steps">
        {steps.map((step, index) => (
          <motion.div
            key={index}
            className="process-card"
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.3 }}
            transition={{ duration: 0.5, delay: index * 0.2 }}
            whileHover={{ scale: 1.05 }}
          >
            <div className="icon-container">{step.icon}</div>
            <h3>{step.title}</h3>
            <p>{step.description}</p>
          </motion.div>
        ))}
      </div>

      <motion.a
        href="tel:+2347086805711"
        className="cta-button"
        initial={{ opacity: 0, y: 20 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6, delay: 1.5 }}
        whileHover={{ scale: 1.1 }}
        onClick={handleCTAClick} // ✅ Smart handler
      >
        📞 Talk to Us — Let’s Get You the Right Car
      </motion.a>

      {/* ✅ SweetPopup Rendered */}
      <SweetPopup isOpen={isPopupOpen} onClose={() => setPopupOpen(false)} />
    </div>
  );
};

export default WorkProcess;
